.logo {
    margin-top: 10px;
    padding: 0;
    height: 100px;
}

.dropbtn {
    background-color: #76C784;
    color: rgb(0, 0, 0);
    padding: 16px;
    border: none;
    cursor: pointer;
    font-size: large;
    font-weight: bold;
    overflow: hidden;
    /* 超過範圍隱藏 */
    white-space: nowrap;
    /* 不斷行 */
}

.dropdown {
    position: relative;
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    right: 0;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}

.dropdown-content a {
    color: black;
    padding: 12px 16px;
    text-decoration: none;
    display: block;
}

.dropdown-content a:hover {
    background-color: #f1f1f1
}

.dropdown:hover .dropdown-content {
    display: block;
}

.dropdown:hover .dropbtn {
    background-color: #3e8e41;
}

